import './AdminPedidos.css'

import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { Page, SDataTable } from '../../components'
import { RUTAS, RUTAS_WEB_TRANSPORTISTA } from '../../constants'
import { openPageNow } from '../../helpers'
import { useStores } from '../../models/root-store/root-store-context'

export const AdminVehiculo: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const DataTable = React.createRef<any>()
  const EstadoVehiculo = useRef<string>()

  React.useEffect(() => {
    DataTable.current.refresh()
  }, [window.location.pathname])

  const setup = async () => {
    if (loginStore.administrador) {
      EstadoVehiculo.current = history.location.pathname.split('/')[3]
    }
  }

  useEffect(() => {
    setup()
  }, [])

  const visualizar = (row: any) => {
    if (loginStore.modo_uso_app === 30) {
      openPageNow(RUTAS_WEB_TRANSPORTISTA.VEHICULO(row.id), history)
    } else {
      openPageNow(RUTAS.ADMIN_VEHICULO_DETAIL(EstadoVehiculo.current, row.id), history)
    }
  }

  const editar = (row: any) => {
    if (loginStore.modo_uso_app === 30) {
      openPageNow(RUTAS_WEB_TRANSPORTISTA.VEHICULO_UPDATE(row.id), history)
    } else {
      openPageNow(RUTAS.ADMIN_VEHICULO_UPDATE(EstadoVehiculo.current, row.id), history)
    }
  }

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}>
          <FIcon.Eye size={14} />
        </span>
        <span className={'button ml-3'} title={'Editar'} onClick={() => editar(row)}>
          <FIcon.Edit size={14} />
        </span>
      </div>
    )
  }

  const agregarVehiculo = () => {
    history.push(RUTAS_WEB_TRANSPORTISTA.VEHICULO_CREATE)
  }

  const breadCrumbs = () => {
    if (loginStore.modo_uso_app === 30) {
      return (
        <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS_WEB_TRANSPORTISTA.INICIO)}>Inicio</a>
          </li>
          <li className="breadcrumb-item active">Vehculos</li>
        </ol>
      )
    } else {
      return (
        <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS.ADMIN)}>Inicio</a>
          </li>
          <li className="breadcrumb-item active">Vehculos</li>
        </ol>
      )
    }
  }

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Vehculos</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">{breadCrumbs()}</div>
          </div>
          {loginStore.modo_uso_app === 30 && (
            <ul className="nav nav-pills p-3 bg-white mb-3 align-items-center">
              <li className="nav-item ml-auto">
                <button className={'btn btn-primary'} title={'Agregar vehculo'} onClick={() => agregarVehiculo()}>
                  <FIcon.Plus size={14} /> Agregar vehculos
                </button>
              </li>
            </ul>
          )}
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  let moderado = history.location.pathname.split('/')[3] !== 'pendiente'
                  return rootStore.environment.api.vehiculos(page, perPage, order, search, moderado)
                }}
                columns={[
                  {
                    name: 'ID',
                    selector: 'id',
                    sortable: true,
                  },
                  {
                    name: 'Marca',
                    selector: 'marca',
                    sortable: true,
                  },
                  {
                    name: 'Modelo',
                    selector: 'modelo',
                    sortable: true,
                  },
                  {
                    name: 'Ao',
                    selector: 'ano',
                    sortable: true,
                  },
                  {
                    name: 'Color',
                    selector: 'color',
                    sortable: true,
                  },
                  {
                    name: 'Tipo de vehculo',
                    selector: 'tipo_vehiculo',
                    sortable: true,
                    cell: (row: any) => row.tipo_vehiculo.nombre,
                  },
                  {
                    name: 'Aprobado',
                    selector: 'aprobado',
                    sortable: false,
                    cell: (row: any) => (row.aprobado ? 'Si' : 'No'),
                  },
                  {
                    name: 'Activo',
                    selector: 'activo',
                    sortable: true,
                    cell: (row: any) => (row.activo ? 'Si' : 'No'),
                  },
                  {
                    name: 'Transportista',
                    selector: 'transportista',
                    omit: loginStore.modo_uso_app === 30,
                    sortable: true,
                    cell: (row: any) => row.transportista,
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminVehiculo
